import React,{useState} from 'react';



const List = ({Text,Del}) =>{
	var  state = {
    C: 'white',
  }
const bg =['red','blue','green','black'];

const Color = ()=>{
	const k =Math.floor(Math.random() * 4) - 1 ;
	return bg[k];
}
	
	
return( <div className="todo">
      <li className="todo-item" >{Text} </li>
      <button className ="complete-btn"  ><i class="fa fa-bolt" ></i></button>
      <button onClick = {() => Del(Text)} className = "trash-btn">
      <i className ="fas fa-trash"></i>
      </button>
    </div>
);
};
 export default List;