import React,{useState} from 'react';

import './App.css';
import Form from './component/Form';
import Todo from './component/todo';
class  App extends React.Component {

constructor(){

super()
this.state={
  input:"",
  list :[]
}
}
  Onclick = async (event) => {
   event.preventDefault();
    // not allowed AND not working
  await  this.setState(state => {
      const list = [...state.list, {text :state.input , id:Math.random()*1000,completed:false}];
 
      return {
        input: '',
        list,
        
      };
    });
 // console.log(this.state.list);

  }
Ontype = async (event) =>{

     await this.setState({input: event.target.value});
    // console.log(this.state.input);
   }

  arrayRemove=(arr, value)=> { 
    
        return arr.filter(function(ele){ 
            return ele.text != value; 
        });
    }

   
 
    Del= async(text)=>{
    console.log(text);
    const list2 =this.state.list;
    const list3 = this.arrayRemove(list2,text)
    
    list2.filter((el)=>el.text!==text);
    await this.setState({list:list3});
   // console.log(this.state);
  }
  
  

 
render(){
  return (
    <div className="App">

<header> 
        <h1>My Todo</h1>
</header> 
<Form Ontype={this.Ontype} Onclick = {this.Onclick} Value={this.state.input} /> 
<Todo Todos = {this.state.list} Stage ={this.state} Del={this.Del} />
    </div>
  );
}
}
export default App;
