import React,{memo} from 'react';
import List from './list';

const Isupdated =(nextprops,currentprops)=>{
	if(nextprops!==currentprops)
		return false;
	else
		return false;
}

const Todo = memo(({Todos,Stage,Del}) =>{
	
return( <div className="todo-container">
      <ul className="todo-list">
      {
Todos.map(Tod=>(       
<List Text={Tod.text}  Del={Del} />

	))}
      

  </ul>
    </div>
);
},Isupdated);
 export default Todo;