import React from 'react';



const Form =({Ontype,Onclick,Value})=>{


return(
    <form>
      <input value= {Value} onChange = {Ontype} type="text" className="todo-input" placeholder ="Add Task" />
      <button className="todo-button"    onClick ={Onclick}  type="submit">
        <i className="fas fa-plus-square"></i>
      </button>
      <div className="select">
        <select name="todos" className="filter-todo">
          <option value="all">Schedule</option>
         

        </select>
      </div>
    </form>
	);
};

export default Form;